<template>
	<div class="radio-group">
		<div class="radio"
		     @click="checkRadio(item,index)" :key="index"
		     v-for="(item,index) in datas">
			<Icon :class="[checkedIndex === index ? 'icon-radiochecked' : 'check' ]"
			      :name="[checkedIndex === index ?'radiochecked' : '']"></Icon>
			<span>{{item.label}}</span>
		</div>
	</div>
</template>
<script>
import Icon from "./icon";
export default {
  props: {
    label: {
      type: String,
      default: ""
    },
    datas: Array
  },
  data() {
    return {
      checkedIndex: 0,
      checkedValue: null
    };
  },
  methods: {
    getValue() {
      return this.checkedValue;
    },
    setDefault(v){
      for(var i=0;i<this.datas.length;i++){
        if(this.datas[i].value == v){
          this.checkedIndex = i;
          this.checkedValue = this.datas[i].value;
          return;
        }
      }
    },
    checkRadio(item, index) {
      this.checkedIndex = index;
      this.$emit("change",item.value);
      this.checkedValue = item.value;
    }
  },
  components: {
    Icon
  }
};
</script>
<style lang="less" scoped>
.radio-group {
  display: flex;
  align-items: center;
  .radio {
    margin: 0 5px;
    display: inline-flex;
    align-items: center;
  }
  .check {
    //box-sizing: border-box;
    width: 21px;
    height: 21px;
    margin-right: 4px;
    border: 1px solid #cccccc;
    border-radius: 50%;
  }
  .icon-radiochecked {
    width: 22px;
    height: 22px;
    margin-right: 4px;
  }
}
</style>


