<template>
  <div>
    <topNav title="参数设置" url="/checkIndex"></topNav>
    <div class="checkSet">
      <div class="checkSetContainer">
        <div class="checkSetForm">
          <!-- <div class="checkSetFormItem">
            <label>包价类别</label>
            <gpicker
              style="flex-basis:76%;"
              @change="gettaCode()"
              class="gpicker"
              ref="categoryPicker"
            ></gpicker>
          </div>-->
          <div class="checkSetFormItem">
            <label>营业点</label>
            <gpicker style="flex-basis:76%;" ref="taCodePicker"></gpicker>
          </div>
          <div class="checkSetFormItem radioItem" style="justify-content:none;height:30px;">
            <label>获取方式</label>
            <div style="flex-basis:76%;display:flex;">
              <radio style="flex-basis:76%;" :datas="getType" @change="setGetType" ref="getType"></radio>
              <gpicker
                v-show="formData.getType==2"
                class="timePicker"
                style="flex-basis:50%;"
                ref="timePicker"
              ></gpicker>
            </div>
          </div>
          <div class="checkSetFormItem radioItem">
            <label>核销方式</label>
            <radio
              style="flex-basis:76%;"
              :datas="verification"
              @change="setVerification"
              ref="verification"
            ></radio>
          </div>
          <div class="checkSetFormItem radioItem">
            <label>扇区号</label>
            <gpicker style="flex-basis:76%;" ref="secNum"></gpicker>
            <!-- <gpicker style="flex-basis:36%;" ref="cardStart"></gpicker> -->
          </div>
          <div class="checkSetFormItem radioItem">
            <label>用户名</label>
            <div class="input-container" style="flex-basis:76%;">
              <input type="text" v-model="formData.user">
            </div>
          </div>
        </div>
      </div>
      <button class="getReportBtn" @click="getAnalyzeBreakfastReport">生成早餐数据</button>
      <button class="saveBtn" @click="submit">保存</button>
    </div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
import gpicker from "../common/gpicker";
import radio from "../common/radio";
export default {
  data() {
    return {
      formData: !!localStorage.checkPs
        ? JSON.parse(localStorage.checkPs)
        : {
            category: "",
            taCode: "",
            taCodeName: "",
            getType: "2",
            time: 2,
            verification: "2",
            // cardStart: 8
            secNum: 0,
            user:"ADMIN"
          },
      getType: [
        {
          label: "手动",
          value: "1"
        },
        {
          label: "自动",
          value: "2"
        }
      ],
      verification: [
        {
          label: "手动",
          value: "1"
        },
        {
          label: "自动",
          value: "2"
        }
      ]
    };
  },

  methods: {
    disCodeName(v) {
      return v.code + " -- " + v.name;
    },
    getPackage() {
      this.$http.get("/ohotel/settings/listRevenues").then(res => {

          if (!res.data.result || res.data.result.length == 0) {
            this.msg("未查到营业点");
          } else {
            var taCode = [];
            for (var i = 0; i < res.data.result.length; i++) {
              taCode.push({
                code: res.data.result[i].revenueCode,
                name: res.data.result[i].revenueName
              });
            }
            this.$refs.taCodePicker.setData(
              taCode,
              this.formData.taCode,
              this.disCodeName
            );
          }
      
      });
    },
    gettaCode() {
      let pk = this.$refs.categoryPicker.getValue();
      this.$http
        .post("/micheck/pms.outlet.list", {
          content: {
            pckgCode: pk
          }
        })
        .then(res => {
          if (res.data.code == 0) {
            var taCode = [];
            for (var i = 0; i < res.data.content.length; i++) {
              taCode.push({
                code: res.data.content[i].code,
                name: res.data.content[i].descript
              });
            }
            this.$refs.taCodePicker.setData(
              taCode,
              this.formData.taCode,
              this.disCodeName
            );
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    submit() {
      this.formData.taCode = this.$refs.taCodePicker.getValue();
      if (!this.formData.taCode) {
        this.msg("请选择营业店");
        return;
      }
      this.formData.time = this.$refs.timePicker.getValue();
      this.formData.secNum = this.$refs.secNum.getValue();
      this.formData.taCodeName = this.getNameFromDis(
        this.$refs.taCodePicker.getDisValue()
      );
      this.$http
        .get("/ohotel/settings/listRevenueSettings", {
          params: {
            condition: {
              revenueCode: this.formData.taCode
            }
          }
        })
        .then(res => {
          if (res.data.success) {
            if (res.data.result.length > 0) {
              this.formData.price = {
                adultPrice: res.data.result[0].adultPrice,
                childPrice: res.data.result[0].childPrice
              };
              localStorage.setItem("checkPs", JSON.stringify(this.formData));
              this.msg("保存成功");
              this.$router.push("/checkIndex");
            }
          } else {
            this.msg("获取价格失败，请重试");
          }
        });
    },
    getNameFromDis(v) {
      if (v) {
        return v.split(" -- ")[1];
      }
      return "";
    },
    setGetType(v) {
      this.formData.getType = v;
    },
    setVerification(v) {
      this.formData.verification = v;
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    },
    getAnalyzeBreakfastReport() {
      this.$http.get("/ohotel/breakfast/analyzeBreakfastReport",{dpost:true}).then(res => {
        if (res.data.success) {
          this.msg("报表生成成功");
        } else {
          this.msg(res.data.msgs);
        }
      });
    }
  },

  mounted() {
    this.getPackage();
    let tm = [];
    for (let i = 1; i < 10; i++) {
      tm.push({
        code: i,
        name: i + "秒之后"
      });
    }
    this.$refs.timePicker.setData(tm, this.formData.time);
    // let ts = [];
    // for (let i = 0; i < 64; i++) {
    //   ts.push({
    //     code: i,
    //     name: i
    //   });
    // }
    // this.$refs.cardStart.setData(ts, this.formData.cardStart);
    let sn = [];
    for (let i = 0; i < 64; i++) {
      sn.push({
        code: i,
        name: i + ""
      });
    }
    // console.log("sn", sn);
    // console.log("sn", this.formData.secNum);
    this.$refs.secNum.setData(sn, this.formData.secNum);
    this.$refs.verification.setDefault(this.formData.verification);
    this.$refs.getType.setDefault(this.formData.getType);
  },
  components: {
    gpicker,
    radio
  }
};
</script>

<style lang="less" scoped>
.checkSet {
  box-sizing: border-box;
  width: 100%;
  height: 100vh;
  background: #f4f5f7;
  padding: 20px 8px;

  .checkSetContainer {
    // box-sizing: border-box;
    // width: 100%;
    padding: 17px 24px;
    background: #ffffff;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.1);
    border-radius: 5px;

    .checkSetForm {
      .checkSetFormItem {
        margin: 10px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .gpicker {
          position: relative;

          width: 64%;
          background: #ffffff;
          border: 1px solid #cccccc;
          border-radius: 3px;
        }

        select {
          width: 64%;
          height: 37px;
          outline: none;
          border: 1px solid #cccccc;
          border-radius: 3px;
        }

        .mint-radiolist {
          display: flex;
        }
      }

      .radioItem {
        // justify-content: flex-start;
      }
    }
    .input-container {
      border: 1px solid #cccccc;
      border-radius: 3px;
      input {
        border: none;
        width: 100%;
        outline: none;
        border-radius: 3px;
        padding: 7px 0;
        text-align: center;
      }
    }
  }

  .saveBtn,
  .getReportBtn {
    width: 135px;
    height: 37px;
    margin:27px 10px;
    background: #ff8902;
    border-radius: 7px;
    outline: none;
    border: none;
    color: #fff;
    font-size: 14px;
  }
}
</style>


